.empty{
	font-size: .3rem;
	color: #6e6e6e;
	text-align: center;
	background-color: #fff;
	background-image: url(../images/empty.png);
	background-position: center .5rem;
	background-repeat: no-repeat;
	background-size: 1.32rem auto;
	padding-top: 2.5rem;
	padding-bottom: .4rem;

	p{
		padding-bottom: .24rem;

		&:last-child{
			padding-bottom: 0;
		}
	}
}