.rush-buy-item{
	display: block;
	padding-bottom: .1rem;
	position: relative;
	border-bottom: 1px solid #e5e5e5;
	margin-top: .06rem;
	background-color: #fff;
	overflow: hidden;

	&:first-child{
		margin-top: 0;
	}

	a{
		display: block;
	}

	h2{
		font-size: .22rem;
		color: #535353;
		padding: .12rem .1rem .12rem 1.35rem;
		line-height: 1;
		position: relative;
		margin-bottom: .1rem;

		span{
			display: inline-block;
			vertical-align: middle;
			color: #535353;
		}

		img{
			/*width: .8rem;*/
			height: .41rem;
			position: absolute;
			left: .3rem;
			top: 50%;
			-webkit-transform: translateY(-50%);
			transform: translateY(-50%);
		}
	}

	.mj{
		color: #f9384e;
		font-size: .16rem;
		vertical-align: middle;
		margin-left: .2rem;
	}

	.icon-two-arrow{
		position: absolute;
		right: .4rem;
		top: 50%;
		-webkit-transform: translateY(-50%);
		transform: translateY(-50%);
		/*display: inline-block;
		margin-left: .2rem;
		vertical-align: middle;*/
	}

	.item-con{
		padding-left: 2.6rem;
		height: 1.84rem;
		position: relative;

		a{
			height: 100%;
		}

		img{
			display: block;
			width: 100%;
			height: 100%;
		}
	}

	.goods-img-wraper{
		width: 1.85rem;
		height: 1.85rem;
		position: absolute;
		left: 0.47rem;
		top: 50%;
		-webkit-transform: translateY(-50%);
		transform: translateY(-50%);

		.wrap-inner{
			border: 1px solid #e6e6e6;
			width: 3.7rem;
			height: 3.7rem;
			-webkit-transform: scale(.5, .5);
		    transform: scale(.5, .5);
		    -webkit-transform-origin: 0 0;
		    transform-origin: 0 0;
		    -webkit-box-sizing:border-box;
			box-sizing:border-box;
		}
	}

	.item-right{
		height: 100%;
		position: relative;
	}

	.title{
		font-size: 0.26rem;
		color: #4a4a4a;
		padding-top: 0.1rem;
		display: -ms-box;
		display: -webkit-box;
		display: box;
		-ms-line-clamp: 2;
	    -webkit-line-clamp: 2;
		line-clamp: 2;
		-ms-box-orient: vertical;
	    -webkit-box-orient: vertical;
		box-orient: vertical;
	    overflow: hidden;
	}

	.price{
		font-size: 0;
		position: absolute;
		bottom: 0.1rem;
		width: 100%;
	}

	.price-proup{
		float: left;
		padding-bottom: .1rem;

		p{
			display: inline-block;
			line-height: 1;
		}
	}

	.new-price{
		color: #e33a3d;
		height: 0.32rem;
		line-height: 0.32rem;

		.price-type{
			font-size: 0.32rem;
			font-family: Arial;
		}
	}

	.new-price-num{
		font-size: 0.32rem;
	}

	.old-price{
		font-size: 0.24rem;
		color: #979797;
		height: 0.2rem;
		line-height: 0.2rem;
		padding-left: .1rem;

		.price-type{
			padding-right: .05rem;
		}
	}

	.old-price-num{
		text-decoration: line-through;
		font-family: Arial;
	}

	.btn{
		position: absolute;
		right: 0;
		bottom: 0;
	}

	.dry-up:after{
		position: absolute;
	    width: 1rem;
	    height: 1rem;
	    text-align: center;
	    line-height: 1rem;
	    font-size: .24rem;
	    background: rgba(0,0,0,.6);
	    color: #fff;
	    left: 50%;
	    top: 50%;
	    -webkit-transform: translate(-50%,-50%);
	    transform: translate(-50%,-50%);
	    content: '已抢光';
	    border-radius: 50%;
	}

	.under-carriage{
		font-size: 0.24rem;
		color: #979797;
	}
}