.party-count-down{
    height: .6rem;
    background: #f2f2f2;
    padding: 0 .3rem;
    position: relative;
    
    .party-remind{
        color: #000;
        height: 100%;
        line-height: .6rem;
        font-size: .22rem;
    }
    .timer-txt{
        padding-right: .2rem;
        font-size: .22rem;
        color: #676767;
        display: inline-block;
        vertical-align: middle;
        line-height: .35rem;
    }
    .timer-out{
        font-size: 0;
        position: absolute;
        right: .2rem;
        top: 50%;
        -webkit-transform: translateY(-50%);
        transform: translateY(-50%);
    }
    .party-timer{
        display: inline-block;
        vertical-align: middle;
        font-size: .24rem;

        span{
            height: .24rem;
            line-height: 0;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            -ms-box-sizing: border-box;
            box-sizing: border-box;
            float: left;
            display: -webkit-box;
            display: -moz-box;
            -webkit-box-pack: center;
            -moz-box-pack: center;
            -webkit-box-align: center;
            -moz-box-align: center;

            &.number{
                width: .3rem;
                height: .35rem;
                text-align: center;
                color: #fff;
                font-size: .18rem;
                background: #000 url(../images/coutdownborder.png) no-repeat left top;
                background-size: 100% 100%;
                padding-top: .02rem;
                border-radius: .04rem;
            }
        }

        .colon{
            width: .11rem;
            height: .3rem;
            text-align: center;
            font-size: .22rem;
            margin-top: -0.01rem;
        }

        .number-box{
            width: .3rem;
            height: .35rem;
            float: left;
        }
    }
}

