.party-time{
	display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    height: .57rem;

    &.none{
    	display: none;
    }

    .party-item{
		-ms-box-flex: 1;
		-webkit-box-flex: 1;
		box-flex: 1;
		-ms-flex: 1;
		-webkit-flex: 1;
		flex: 1;
		position: relative;
		background: #333333;
		color: #a1a1a1;

		.item-inner{
			font-size: 0;
			position: absolute;
			left: 50%; 
			top: 50%;
			-webkit-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
			white-space: nowrap;
		}

		p{
			text-align: center;
			display: inline-block;
			vertical-align: middle;
			line-height: 1;
		}

		.time-txt{
			font-size: .46rem;
			padding-right: .14rem;
			font-weight: 900;
			line-height: 1;
			font-family: PingFangSC-Regular, Helvetica,"Droid Sans",Arial,sans-serif;
		}

		.party-txt{
			font-size: .24rem;
			line-height: 1;
			font-family: PingFangSC-Regular, Helvetica,"Droid Sans",Arial,sans-serif;
		}

		&.active{
			color: #fff;
			background: #f8363a;
		}
	}
}